body {
  margin: 0;
  font-family: 'Inter', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #23252C;
  width: 100%;

  
}



.App {
  
  height: 780px;
  width: 317px;
  
  background-color: #1A1B21;
  margin: 0 auto;
  margin-top: 53px;
  margin-bottom: 44px;
  border-radius: 10px;
}

/* Header */

.header--image {
  width: 317px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.header--name {
  margin-top: 21px;
  margin-bottom: 0px;
  padding-bottom: 0px;
  text-align: center;
  font-weight: 700;
  font-size: 25px;
  line-height: 31px;
  color: seashell;

}

.header--title {
  margin-top: 7px;
  color: #F3BF99;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  font-weight: 500;
}

/* NavBar */

.NavButtons {
  display: flex;

}
            
.nav--button1, .nav--button2 {
  border-radius: 5px;
  display: flex;
  justify-content: center;

}

.nav--button1{
  background-color: seashell;
  margin-left: 35px;
}

.nav--button2 {
  
  margin-left: 17px;
  color: seashell;
}

.nav--LinkedInButton, .nav--EmailButton {
  font-size: 16px;
  line-height: 16px;
  height: 34px;
  width: 115px;
  text-align: center;
  border-radius: 5px;
}

.nav--EmailButton {
  color: #1E1F26;
}

.nav--LinkedInButton {
  color: seashell;
  background-color: #5093E2;
}



.Nav--imageOne {
  margin-right: 10px;
  width: 13px;
  height: 9.5px;
  color: #1E1F26;
  padding-top: 11.5px;
}

.Nav--imageTwo {
  color: seashell;
  height: 12.5px;
  width: 13px;
  margin-right: 9px;
  padding-top: 8.5px;
}



/*NavBar Button Hover Actions*/

a:link, a:visited,a:hover, :active {
  text-decoration: none;
}

.nav--LinkedInButton:hover, .nav--EmailButton:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

a:active {
  background-color: #1A1B21;
  text-shadow: 0px 0px;
}

.footer--image:hover {
    border: 0px solid  #3b5998;
    box-shadow: 0 0 15px  #3b5998;
    transition: all 0.5s ease;
}


/*Main Section*/

.Main {
  padding-left: 36px;
  padding-right: 36px;
  height: 260px;
}
                    
.main--title {
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  color: #F5F5F5;
  margin-bottom: 5px;
}

.main--p {
  font-style: normal;
  font-size: 12px; /*UNCOMMENT WHEN ADDING PROJECT SECTION*/
  margin-top: 0px;
  line-height: 15px;
  font-size: 14px; 
  line-height: 130%; /* Remove when adding project section*/
  margin-top: 25px;  /* Remove when adding project section*/
  color: #DCDCDC;
}

.main--listBox {
  width: 100%;
  display: flex;
  line-height: 190%;
}

.main--list1 {
  flex: 1;
  width: 40%;
  margin-left: 0px;
}

.main--list2 {
  margin-left: 10px;
  width: 40%;
}

ul {
  list-style: none;
  padding-left: 0px;
  margin-top: 0px;
}

/* Link Decorations */
a.main--ProjectButton {
  color: seashell;
}

 a:hover.main--ProjectButton {
  color: #0077b5;
  font-weight: bold;
}

a:active.main--ProjectButton {
  font-weight: 500;
}

/*Footer Section*/

.FooterBox {
  background-color: #161619;
  height:70px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  text-align: center;


}

.footer--image {
  height: 33px;
  width: 33px;
  background-color: #161619;

}

.footer--text {
  color: seashell;
  margin-top: -10px;
  font-weight: 700
}

.footer--button {
  padding-top: 5px;
}

.GuthubButton{
  border: 0px;
  height: 33px;
  width: 33px;
  border-radius: 5px;
  color: #161619;
  background-color: #161619;
}